import {createSlice} from "@reduxjs/toolkit";

const themeSlice = createSlice({
    name: "theme",
    initialState:{
        darkMode: false
    },
    reducers:{
        switchMode: (state, action)=> {
            state.darkMode = !state.darkMode
        }
    }
})

export const {switchMode} = themeSlice.actions
export default themeSlice.reducer