import React from 'react';
import {Route, Routes, useLocation} from "react-router-dom";
import Home from "../../pages/Home";
import SkillsPage from "../../pages/SkillsPage";
import Projects from "../../pages/Projects";
import AboutPage from "../../pages/AboutPage";
import {AnimatePresence} from "framer-motion";
import NotFound from "../404/NotFound";
import ContactPage from "../../pages/ContactPage";

const AnimatedRouter = () => {
    const location = useLocation()
    const url_line = "/portfolio"
    return (
        <AnimatePresence>
            <Routes location={location} key={location.pathname}>
                <Route path={`${url_line}/`} index element={<Home/>}/>
                <Route path={`${url_line}/skills`} element={<SkillsPage/>}/>
                <Route path={`${url_line}/projects`} element={<Projects/>}/>
                <Route path={`${url_line}/about`} element={<AboutPage/>}/>
                <Route path={`${url_line}/contact`} element={<ContactPage/>}/>
                <Route path="*" element={<NotFound/>}></Route>
            </Routes>
        </AnimatePresence>
    );
}

export default AnimatedRouter;