import React from 'react';
import './Background.scss'
import bg_white from './bg.png'
import bg_black from './bg_black80.png'
import {useSelector} from "react-redux";

const Background = () => {
    const darkMode = useSelector(state => state.theme.darkMode)

    return (
        <div className="background">
            <div className="rainbow">
                {/*<img className='img-bg' src={bg_white} style={{opacity: darkMode ? 0 : 1}} alt="bg"/>*/}
                {/*<img className='img-bg' src={bg_black} style={{opacity: darkMode ? 1 : 0}} alt="bg"/>*/}
                <img className='img-bg' src={bg_white} style={{display: darkMode ? "none" : "block"}} alt="bg"/>
                <img className='img-bg' src={bg_black} style={{display: darkMode ? "block" : "none"}} alt="bg"/>
            </div>
        </div>
    );
}

export default Background;