import React from 'react';
import {motion} from "framer-motion";
import About from "../components/About/About"

const AboutPage = () => {
    return (
        <motion.section
            className="section about_page"
            initial={{
                translateX: "100vw",
                position: "absolute",
                top: 0,
                transition:{duration: 1}}}
            animate={{
                translateX: "0",
                position: "static",
                transition:{duration: 1}}}
            exit={{
                translateY: "100vh",
                transition:{duration: .3}}}
        >
            <About />
        </motion.section>
    );
}

export default AboutPage;