import './app.scss'
import './dark.scss'
import {
    BrowserRouter as Router, Navigate,
} from "react-router-dom";
import Background from "./components/Background/Background";
import Header from "./components/Header/Header";
import {useSelector} from "react-redux";
import AnimatedRouter from "./components/AnimatedRouter/AnimatedRouter";
import Menu from "./components/Menu/Menu";
import {useEffect, useState} from "react";

function App() {
    const darkMode = useSelector(state => state.theme.darkMode)
    const menuActive = useSelector(state => state.menu.menuActive)

    // Mobile Menu
    let [menuStatus, setMenuStatus] = useState(false)

    const menuAllowanceWidth = 768

    let menuAllowance = () => {
        if (window.innerWidth >= menuAllowanceWidth) {
            setMenuStatus(false)
        } else{
            setMenuStatus(true)
        }
    }

    window.addEventListener('resize', menuAllowance)

    useEffect(()=>{
        menuAllowance()
    }, [])

    return (
        <div className={`app${darkMode ? " dark" : ""}${menuActive ? " blocked" : ""}`}>
            <Router>
                {/*<Navigate to="/"/>*/}
                <Background/>
                <Header menuStatus={menuStatus}/>
                <main className="main">
                    <AnimatedRouter />
                </main>
                {menuStatus && <Menu />}
            </Router>
        </div>
    );
}

export default App;
