import React, {useState} from 'react';
import "./Contact.scss"
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import { faTelegram } from '@fortawesome/free-brands-svg-icons'
import { faLinkedin } from '@fortawesome/free-brands-svg-icons'
import { faGithub } from '@fortawesome/free-brands-svg-icons'
import { faWhatsapp } from '@fortawesome/free-brands-svg-icons'
import Form from "./Form";

const Contact = () => {

    let copyClipboard = (e) => {
        let text = e.target.innerHTML;

        const copyContent = async () => {
            try {
                await navigator.clipboard.writeText(text);
                document.querySelector('#tooltip_clipboard').classList.add('active')
                setTimeout(() => {
                    document.querySelector('#tooltip_clipboard').classList.remove('active')
                }, 1500)
            } catch (err) {
                console.error('Failed to copy: ', err);
            }
        }
        copyContent()

    }

    return (
        <div className="contact">
            <div className="container">
                <div className="contact-inner card card-hover">

                    <div className="contact__block form">
                        <h2 className="contact__title">Contact me via form</h2>
                        <Form />
                    </div>

                    <div className="contact__block info">
                        <div>
                            <span>
                                You can message me directly via email &nbsp;
                            </span>
                            <div className="highlight">
                                <div className="tooltip" id="tooltip_clipboard">
                                    <div className="tooltip-inner">
                                        <span>Copied to clipboard!</span>
                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M20.285 2l-11.285 11.567-5.286-5.011-3.714 3.716 9 8.728 15-15.285z"/></svg>
                                    </div>
                                </div>
                                <strong id="my_email" onClick={e => copyClipboard(e)}>erasusdk@gmail.com</strong>
                            </div>
                            <br/>
                            <br/>
                            <span>
                                or call me by this number &nbsp;<strong className="phone-number"><a href="tel:+48510740817">+48 510 740 817</a></strong>
                            </span>
                            <br/>
                            <br/>
                            <div className="contact__socials">
                                <a href="https://www.linkedin.com/in/daniil-kononenko-6aa84a201/" target="_blank" className="contact__socials__item linkedin"><FontAwesomeIcon icon={faLinkedin} /></a>
                                <a href="https://github.com/bjorntheemperor" target="_blank" className="contact__socials__item github"><FontAwesomeIcon icon={faGithub} /></a>
                                <a href="https://t.me/erasusdk" target="_blank" className="contact__socials__item telegram"><FontAwesomeIcon icon={faTelegram} /></a>
                                <a href="https://wa.me/+48510740817" target="_blank" className="contact__socials__item whatsapp"><FontAwesomeIcon icon={faWhatsapp} /></a>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    );
}

export default Contact;