import React from 'react';
import Skills from "../components/Skills/Skills";
import {motion} from "framer-motion";

const SkillsPage = () => {
    return (
        <motion.section
            className="section skills_page"
            initial={{
                translateX: "100vw",
                position: "absolute",
                top: 0,
                transition:{duration: 1}}}
            animate={{
                translateX: "0",
                position: "static",
                transition:{duration: 1}}}
            exit={{
                translateY: "100vh",
                transition:{duration: .3}}}
        >
            <Skills />
        </motion.section>
    );
}

export default SkillsPage;