import React, {useRef, useEffect, useState} from 'react';
import './Intro.scss'
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faGithub, faLinkedin, faTelegram, faWhatsapp} from "@fortawesome/free-brands-svg-icons";
import {faRotateRight} from "@fortawesome/free-solid-svg-icons/faRotateRight";
import {useDispatch, useSelector} from "react-redux";
import {introOff, introOn} from "../../redux/introSlice";

const AnimString = (props) => {
    let nameArray = props.str.split('')

    let final = []

    nameArray.forEach((letter, index) => {
        final.push(<span key={`letter${index}`}>{letter}</span>)
    })

    return (
        <div className={`intro__string${props.oneLine ? " oneLine" : ""}`}
             data-end-animation={props.endAnimation}>
            {final}
        </div>
    )
}

const Intro = () => {
    // console.log("=================== rerendered ===================")
    const dispatch = useDispatch()

    let introAnimationState = useSelector(state => state.intro.animateIntro) // TODO: problem may be here (maybe not LUL)
    // console.log("Intro "+introAnimationState)

    let introTurnOff = (introAnimationState) => {
        dispatch(introOff())
        // setIntroState(false)
    }
    let introTurnOn = (introAnimationState) => {
        dispatch(introOn())
        // setIntroState(true)
    }

    // console.log("useState "+introState)

    useEffect(() => {
        // console.log("state in effect "+introAnimationState)
        // introTurnOff(introAnimationState)
        animateStrings(introAnimationState)
    }, []);

    let resetAnimation = () => {
        let strings = document.querySelectorAll('.intro__string')
        strings.forEach((string, index) => {
            string.classList.remove('hide', 'name', 'frontend', 'location')
            let letters = string.childNodes
            letters.forEach((letter, letterIndex)=> {
                letter.classList.remove('anim')
            })
        })
    }
    let animateStrings = (introAnimationState) => {
        // console.log("in animate func "+introAnimationState)

        resetAnimation()

        let strings = document.querySelectorAll('.intro__string')

        let firstDelay = 1000

        let timeRow = [firstDelay]

        let delayBetween = 500

        !introAnimationState ? delayBetween = 0 : delayBetween = 500

        strings.forEach((string, index) => {

            let letterDelay = 75

            // if there's no intro animation
            // then no letter animation and no string animations
            if (!introAnimationState){
                letterDelay = 0;
                string.style.transition = "0s";
                string.style.animationDuration = "0s"
            } else {
                letterDelay = 75;
                string.style.transition = "0.2s";
                string.style.animationDuration = "0.5s"
            }

            let letters = string.childNodes

            letters.forEach((letter, letterIndex)=>{
                if (letterIndex === letters.length - 1){
                    let animTime = letterDelay * letterIndex + letterDelay + delayBetween
                    timeRow.push(animTime)
                }
            })

            let sumOfPrevDelays = 0

            let mainDelay = []

            for (let i = 0; i < timeRow.length; i++){
                sumOfPrevDelays += timeRow[i]
                mainDelay.push(sumOfPrevDelays)
            }

            setTimeout(()=>{
                letters.forEach((letter, letterIndex)=>{
                    setTimeout(()=>{
                        letter.classList.add('anim')
                    }, letterDelay*letterIndex)


                    // showing socials
                    if (index === strings.length - 1){
                        if (letterIndex === letters.length - 1){
                            setTimeout(()=>{
                                // debugger
                                introTurnOff(introAnimationState)
                                // setTimeout(()=>{
                                // }, 1500)
                                animateSocials(delayBetween)
                            }, letterDelay*letterIndex)
                        }
                    }
                })

            }, mainDelay[index])

            setTimeout(()=>{
                let endAnimation = string.dataset.endAnimation
                endAnimation && string.classList.add(endAnimation)
            }, mainDelay[index+1])

        })
    }

    // animateStrings(introAnimationState)

    let animateSocials = (delayBetween) => {
        let socials = document.querySelectorAll('.intro__socials-item')

        let delay = 300

        setTimeout(()=>{
            socials.forEach((item, index) => {
                setTimeout(()=>{
                    item.classList.add('anim')
                }, delay*index)
            })
        }, delayBetween+500)
    }

    let repeatAnimation = () => {
        introTurnOn(introAnimationState)
        // console.log("in repeat "+introAnimationState)
        // animateStrings(introAnimationState)
    }


    return (
        <div className="intro">
            <div className="container">
                <div className="intro-inner card card-hover">
                    <div className="intro__welcome">
                        <AnimString str="Hello!" oneLine={true} endAnimation="hide"/>
                        <AnimString str="My name is" oneLine={true} endAnimation="hide"/>
                        <AnimString str="Daniil Kononenko." oneLine={true} endAnimation="name"/>
                        <AnimString str="I'm a" oneLine={true} endAnimation="hide"/>
                        <AnimString str="frontend developer." oneLine={true} endAnimation="frontend"/>
                        <AnimString str="Originally" oneLine={false} endAnimation="hide"/>
                        <AnimString str="from Ukraine." oneLine={true} endAnimation="hide"/>
                        <AnimString str="Currently in" oneLine={true} endAnimation="hide"/>
                        <AnimString str="Warsaw, Poland." oneLine={true} endAnimation="location"/>

                        <div className="intro__socials">
                            <a href="https://www.linkedin.com/in/daniil-kononenko-6aa84a201/" target="_blank" className="intro__socials-item linkedin"><FontAwesomeIcon icon={faLinkedin} /></a>
                            <a href="https://github.com/bjorntheemperor" target="_blank" className="intro__socials-item github"><FontAwesomeIcon icon={faGithub} /></a>
                            <a href="https://t.me/erasusdk" target="_blank" className="intro__socials-item telegram"><FontAwesomeIcon icon={faTelegram} /></a>
                            <a href="https://wa.me/+48510740817" target="_blank" className="intro__socials-item whatsapp"><FontAwesomeIcon icon={faWhatsapp} /></a>
                        </div>

                        {/*<button title="set false" className="intro__repeat" onClick={handleOff}>*/}
                        {/*    /!*<FontAwesomeIcon icon={faRotateRight} />*!/*/}
                        {/*    <FontAwesomeIcon icon={faXmark} />*/}
                        {/*</button>*/}
                        {/*<button style={{transform: "translateY(100px)"}} title="set true" className="intro__repeat" onClick={handleOn}>*/}
                        {/*    <FontAwesomeIcon icon={faCheck} />*/}
                        {/*</button>*/}
                        {/*<button title="set true" className="intro__repeat" onClick={repeatAnimation}>*/}
                        {/*    <FontAwesomeIcon icon={faRotateRight} />*/}
                        {/*</button>*/}

                    </div>
                </div>
            </div>
        </div>
    );
}

export default Intro;