import React from 'react';
import Contact from "../components/Contact/Contact";
import {motion} from "framer-motion";

const ContactPage = () => {
    return (
        <motion.section
            className="section"
            initial={{
                translateX: "100vw",
                position: "absolute",
                top: 0,
                transition:{duration: 1}}}
            animate={{
                translateX: "0",
                position: "static",
                transition:{duration: 1}}}
            exit={{
                translateY: "100vh",
                transition:{duration: .3}}}
        >
            <Contact/>
        </motion.section>
    );
}

export default ContactPage;