import React, {useRef, useState} from 'react';
import emailjs from '@emailjs/browser';
import loader from "../../img/loader.gif"

const Form = () => {

    const [loading, setLoading] = useState(false)

    let autoResize = (e) => {
        let el = e.target ? e.target : e
        el.style.cssText = 'height:auto;';
        el.style.cssText = 'height:' + el.scrollHeight + 'px';
    }

    let setActiveGroup = (e) => {
        e.target.value
            ? e.target.parentNode.classList.add("active")
            : e.target.parentNode.classList.remove("active")
    }

    window.addEventListener('resize', ()=>{
        autoResize(document.body.querySelector("#textarea"))
    })

    const form = useRef()

    const sendEmail = (e) => {
        e.preventDefault();

        setLoading(true)

        emailjs.sendForm(
            'service_8s77x1v',
            'template_lkq2n9m',
            form.current,
            'lasQH7X9AiYrgMcEH')
            .then((result) => {
                setLoading(false)
                document.querySelector('#tooltip_form').classList.add('active')
                setTimeout(() => {
                    document.querySelector('#tooltip_form').classList.remove('active')
                }, 3000)
            }, (error) => {
                setLoading(false)
                console.log(error.text);
            });
    };

    return (
        <form ref={form} className={`contact__form${loading && " disabled"}`} id="my_form" onSubmit={sendEmail}>
            <div className="contact__form-inner">
                {loading && <img src={loader} alt="loader" className="contact__loader"/>}

                <div className="contact__form-group input">
                    <input
                        onInput={e => setActiveGroup(e)}
                        type="text" id="name" name="user_name" className="contact__input" required disabled={loading}/>
                    <label htmlFor="name" className="contact__label">Your name</label>
                </div>
                <div className="contact__form-group input">
                    <input
                        onInput={e => setActiveGroup(e)}
                        type="text" id="email" name="user_email" className="contact__input" required disabled={loading}/>
                    <label htmlFor="email" className="contact__label">Your email</label>
                </div>
                <div className="contact__form-group textarea">
                    <textarea
                        onInput={e => {
                            autoResize(e)
                            setActiveGroup(e)
                        }}
                        rows="1" name="message" id="textarea" className="contact__textarea" required disabled={loading}>
                    </textarea>
                    <label htmlFor="textarea" className="contact__label">
                        Your message
                    </label>
                </div>
                <div className="contact__form-group btn">
                    <div className="tooltip" id="tooltip_form">
                        <div className="tooltip-inner">
                            <span>Message has been sent successfully!</span>
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M20.285 2l-11.285 11.567-5.286-5.011-3.714 3.716 9 8.728 15-15.285z"/></svg>
                        </div>
                    </div>
                    <input type="submit" value="Send" className="contact__send" disabled={loading}/>
                </div>
            </div>
        </form>
    );
}

export default Form;