import React from 'react';
import "./Skills.scss"
import {data} from "./data";
import Stars from "./Stars";

let SkillsItem = (props) => {
    let item = props.item

    return (
        <div className="skills__item">
            <img src={item.img} alt={item.title} className="skills__item__img-bg"/>
            <div className="skills__item__img-wrapper">
                <img src={item.img} alt={item.title} title={item.title} className="skills__item__img" draggable="false"/>
            </div>
            <div className="skills__item-title">{item.title}</div>
            <div className="skills__item__rating">
                <Stars item={item}/>
            </div>
        </div>
    )
}

let SkillsGrid = (props) => {
    return (
        <div className="skills-grid">
            {props.items.map((item, index)=>{
                return (
                    <SkillsItem item={item} key={"SkillsItem"+index} index={index}/>
                )
            })}
        </div>
    )
}

const Skills = () => {
    return (
        <div className="skills">
            <div className="container">
                <div className="skills-inner card card-hover">
                    <SkillsGrid items={data}/>
                </div>
            </div>
        </div>
    );
}

export default Skills;