import React from 'react';
import "./Menu.scss"
import {useDispatch, useSelector} from "react-redux";
import {switchActiveMenu} from "../../redux/menuSlice";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faXmark} from "@fortawesome/free-solid-svg-icons";
import {NavLink} from "react-router-dom";

const Menu = () => {
    const dispatch = useDispatch()

    let handleMenuClick = () => {
        dispatch(
            switchActiveMenu(menuActive)
        )
    }
    const menuActive = useSelector(state => state.menu.menuActive)

    const url_line = "/portfolio"

    return (
        <div className={`menu${!menuActive ? " hidden" : ""}`}>
            <div className="menu-inner">
                <div className="menu__nav">
                    <NavLink to={`${url_line}/`} className="menu__nav-item home" onClick={handleMenuClick}><span>Home</span></NavLink>
                    <div className="menu__nav-line"></div>
                    <NavLink to={`${url_line}/skills`} className="menu__nav-item skills" onClick={handleMenuClick}><span>Skills</span></NavLink>
                    <div className="menu__nav-line"></div>
                    <NavLink to={`${url_line}/projects`} className="menu__nav-item projects" onClick={handleMenuClick}><span>Projects</span></NavLink>
                    <div className="menu__nav-line"></div>
                    <NavLink to={`${url_line}/about`} className="menu__nav-item about" onClick={handleMenuClick}><span>About me</span></NavLink>
                    <div className="menu__nav-line"></div>
                    <NavLink to={`${url_line}/contact`} className="menu__nav-item contact" onClick={handleMenuClick}><span>Contact</span></NavLink>
                </div>
                <button onClick={handleMenuClick} className="menu-close">
                    <FontAwesomeIcon icon={faXmark} />
                </button>
            </div>
        </div>
    );
}

export default Menu;