import React from 'react';
import './About.scss'
import Timeline from "./Timeline";
import profile from './img/main.jpg'

const About = () => {
    return (
        <div className="about">
           <div className="container">
               <div className="about-inner card card-hover">
                   <div className="about__block">
                       <div className="about__top">
                           <h1 className="about__name">
                               <span className="name">Daniil </span>
                               <span className="surname">Kononenko</span>
                           </h1>
                           <img src={profile} alt="img" className="about__img"/>
                       </div>
                   </div>
                   <div className="about__block">
                       <h2 className="about__title">Info</h2>
                       <p className="about__text">
                           <span><strong>Age: </strong>22 y. o.</span>
                           <br/>
                           <span><strong>Languages: </strong> English <small>(B2)</small>, Ukrainian <small>(native)</small>, Russian <small>(native)</small></span>
                           <br/>
                           <span><strong>Phone number: </strong> <a href="tel:+48510740817">+48 510 740 817</a></span>
                           <br/>
                           <span><strong>Current location: </strong> Warsaw 01-023, POLAND</span>
                       </p>
                   </div>
                   <div className="about__block">
                       <h2 className="about__title">Education</h2>
                       <p className="about__text">
                           <strong>Bachelors degree: </strong>Computer Engineering, 06/2021
                           <br/>
                           <strong>Chernihiv State Technological University </strong> hero city Chernihiv, UKRAINE
                       </p>
                   </div>
                   <div className="about__block">
                       <h2 className="about__title">Work history</h2>
                       <Timeline/>
                   </div>

               </div>
           </div>
        </div>
    );
}

export default About;