import {createSlice} from "@reduxjs/toolkit";

const introSlice = createSlice({
    name: "intro",
    initialState:{
        animateIntro: true
    },
    reducers:{
        introOff: (state, action)=> {
            state.animateIntro = false
        },
        introOn: (state, action)=> {
            state.animateIntro = true
        }
    }
})

export const {introOff, introOn} = introSlice.actions
export default introSlice.reducer