import React, {useState} from 'react';

const Timeline = () => {
    const [min, setMin] = useState(true)

    let handleClickToMinimize = () => {
        setMin(true)
    }
    let handleClickToExpand = () => {
        setMin(false)
    }

    let monthStreak = 17

    const [monthItemHeight, setMonthItemHeight] = useState(60)

    let onSetMonthItemHeight = () => {
        if (window.innerWidth <= 768){
            setMonthItemHeight(40)
        } else{
            setMonthItemHeight(60)
        }
    }

    window.addEventListener('resize', onSetMonthItemHeight)

    return (
        <div className={`about__timeline${min ? " minimized" : ""}`} onClick={min ? handleClickToExpand : undefined}>
            <div className="about__timeline-inner">
                <button className="about__timeline-btn" onClick={handleClickToMinimize}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M0 3.795l2.995-2.98 11.132 11.185-11.132 11.186-2.995-2.981 8.167-8.205-8.167-8.205zm18.04 8.205l-8.167 8.205 2.995 2.98 11.132-11.185-11.132-11.186-2.995 2.98 8.167 8.206z"/></svg>
                </button>
                <div className="about__timeline__year">
                    <div className="about__timeline__months">
                        <div className="about__timeline__months__item">
                            <h3 className="about__timeline__work">
                                <span className="about__timeline__work-title">Freelance</span>
                                <span className="about__timeline__work-line"
                                      style={{height: `calc(${monthItemHeight}px*3 - 20px)`}}
                                >
                                    <span className="about__timeline__work-line--inner"></span>
                                </span>
                            </h3>
                            <span><span>August</span><span className="only_min"> /21</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <div className='about__timeline__months__item-position'>as frontend developer and page layout specialist</div>
                            <span><span>September</span><span className="only_min"> /21</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>October</span><span className="only_min"> /21</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>November</span><span className="only_min"> /21</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>December</span><span className="only_min"> /21</span></span>
                        </div>
                    </div>
                    <h3 className="about__timeline__year-title">2021</h3>
                </div>
                <div className="about__timeline__year">
                    <div className="about__timeline__months">
                        <div className="about__timeline__months__item current">
                            <span><span>January</span><span className="only_min"> /22</span></span>
                        </div>
                        <div className="about__timeline__months__item work_start">
                            <h3 className="about__timeline__work">
                                <span className="about__timeline__work-title">Sbitsoft Web Development<span className="only_min about__timeline__work-currently"> - Currently</span></span>
                                <span className="about__timeline__work-line"
                                      style={{height: `calc(${monthItemHeight}px*${monthStreak - 1} - 20px)`}}
                                >
                                    <span className="about__timeline__work-line--inner current"></span>
                                </span>
                            </h3>
                            <span><span>February</span><span className="only_min"> /22</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <div className='about__timeline__months__item-position'>as frontend developer and page layout specialist</div>
                            <span><span>March</span><span className="only_min"> /22</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>April</span><span className="only_min"> /22</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>May</span><span className="only_min"> /22</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>June</span><span className="only_min"> /22</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>July</span><span className="only_min"> /22</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>August</span><span className="only_min"> /22</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>September</span><span className="only_min"> /22</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>October</span><span className="only_min"> /22</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>November</span><span className="only_min"> /22</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>December</span><span className="only_min"> /22</span></span>
                        </div>
                    </div>
                    <h3 className="about__timeline__year-title">2022</h3>
                </div>
                <div className="about__timeline__year">
                    <div className="about__timeline__months">
                        <div className="about__timeline__months__item">
                            <span><span>January</span><span className="only_min"> /23</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>February</span><span className="only_min"> /23</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>March</span><span className="only_min"> /23</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>April</span><span className="only_min"> /23</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>May</span><span className="only_min"> /23</span></span>
                        </div>
                        <div className="about__timeline__months__item">
                            <span><span>June</span><span className="only_min"> /23</span></span>
                        </div>
                    </div>
                    <h3 className="about__timeline__year-title">2023</h3>
                </div>
            </div>
        </div>
    );
}

export default Timeline;