import React from 'react';

const NotFound = () => {
    return (
        <div className="container">
            <div className="card card-hover">
                <h1 style={{
                    textAlign: "center",
                    fontSize: "64px",
                    fontWeight: 700
                }}>
                    404
                </h1>
                <h2 style={{
                    textAlign:"center",
                    fontSize:"32px",
                    fontWeight: 700
                }}>
                    Page not found
                </h2>
            </div>
        </div>

    );
}

export default NotFound;