import React from 'react';
import Proj from "../components/Proj/Proj";
import {motion} from "framer-motion";

const Projects = () => {
    return (
        <motion.section
            className="section projects"
            initial={{
                translateX: "100vw",
                position: "absolute",
                top: 0,
                transition:{duration: 1}}}
            animate={{
                translateX: "0",
                position: "static",
                transition:{duration: 1}}}
            exit={{
                translateY: "100vh",
                transition:{duration: .3}}}
        >
            <Proj />
        </motion.section>
    );
}

export default Projects;