import React from 'react';
import {motion} from "framer-motion";
import Intro from "../components/Intro/Intro";

const Home = () => {
    return (
        <motion.section
            className="section"
            initial={{
                translateX: "100vw",
                position: "absolute",
                top: 0,
                transition:{duration: 1}}}
            animate={{
                translateX: "0",
                position: "static",
                transition:{duration: 1}}}
            exit={{
                translateY: "100vh",
                transition:{duration: .3}}}
        >
            <Intro/>
        </motion.section>
    );
}

export default Home;