import React, {useState} from 'react';
import './Proj.scss'

import {data} from "./data";

import loader_route from './../../img/loader_old.gif'
import html_img from "./../../img/tech/min/html.png"
import css_img from "./../../img/tech/min/css.png"
import sass_img from "./../../img/tech/min/sass.png"
import tailwind_img from "./../../img/tech/min/tailwind.png"
import js_img from "./../../img/tech/min/js.png"
import ts_img from "./../../img/tech/min/typescript.png"
import jquery_img from "./../../img/tech/min/jquery.png"
import react_img from "./../../img/tech/min/react.png"
import redux_img from "./../../img/tech/min/redux.png"
import mongodb_img from "./../../img/tech/min/mongodb.png"
import gulp_img from "./../../img/tech/min/gulp.png"
import nodejs_img from "./../../img/tech/min/nodejs.png"
import expressjs_img from "./../../img/tech/min/express.png"
import amp_img from "./../../img/tech/min/amp.png"
import threejs_img from "./../../img/tech/min/threejs.png"

const Proj = () => {

    const [loading, setLoading] = useState(true)

    let handleOnLoad = () => {
        setLoading(false)
    }

    let ProjItem = (props) => {
        const item = props.item
        const index = props.index

        let initialDelay = 1 // seconds
        let delayFraction = .2

        return (
            <div className={"proj__item " + item?.color} style={{animationDelay: `${initialDelay+(index*delayFraction)}s`}}>
                <div className="proj__item-inner" style={{background: item.bgColor}}>
                    <div className="proj__item-top">
                        <h2 className="proj__item-title">{item.name}</h2>

                        <div className="proj__item-year">Year: <span className="proj__item-ml">{item.year}</span></div>

                        <div className="proj__item-type">Type: <span className="proj__item-ml">{item.type}</span></div>

                        <div className="proj__item-tech">
                            <div className="proj__item-tech--title">
                                Technologies used
                            </div>
                            <div className="proj__item-tech__wrap-outer">
                                <div className='proj__item-tech__wrap'>
                                    {item.tech.html &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={html_img} title="HTML" alt="HTML" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                    {item.tech.css &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={css_img} title="CSS" alt="CSS" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                    {item.tech.sass &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={sass_img} title="SASS/SCSS" alt="SASS/SCSS" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                    {item.tech.tailwind &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={tailwind_img} title="Tailwind" alt="Tailwind" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                    {item.tech.js &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={js_img} title="JavaScript" alt="JavaScript" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                    {item.tech.ts &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={ts_img} title="TypeScript" alt="TypeScript" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                    {item.tech.jquery &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={jquery_img} title="jQuery" alt="jQuery" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                    {item.tech.react &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={react_img} title="React" alt="React" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                    {item.tech.redux &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={redux_img} title="Redux" alt="Redux" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                    {item.tech.mongodb &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={mongodb_img} title="MongoDB" alt="MongoDB" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                    {item.tech.gulp &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={gulp_img} title="gulp.js" alt="gulp.js" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                    {item.tech.nodejs &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={nodejs_img} title="NodeJS" alt="NodeJS" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                    {item.tech.expressjs &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={expressjs_img} title="ExpressJS" alt="ExpressJS" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                    {item.tech.amp &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={amp_img} title="Accelerated Mobile Pages" alt="Accelerated Mobile Pages" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                    {item.tech.threejs &&
                                        <div className="proj__item-tech--img---wrapper">
                                            <img src={threejs_img} title="ThreeJS" alt="ThreeJS" className="proj__item-tech--img"/>
                                        </div>
                                    }
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="proj__item-bot">
                        <div className="proj__item__links">
                            <a href={item.link} target="_blank" className="proj__item__links-item">
                                <span>Check out here!</span>
                                <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 20 20" fill="none">
                                    <path d="M11 3C10.4477 3 10 3.44772 10 4C10 4.55228 10.4477 5 11 5H13.5858L7.29289 11.2929C6.90237 11.6834 6.90237 12.3166 7.29289 12.7071C7.68342 13.0976 8.31658 13.0976 8.70711 12.7071L15 6.41421V9C15 9.55228 15.4477 10 16 10C16.5523 10 17 9.55228 17 9V4C17 3.44772 16.5523 3 16 3H11Z" fill="black"/>
                                    <path d="M5 5C3.89543 5 3 5.89543 3 7V15C3 16.1046 3.89543 17 5 17H13C14.1046 17 15 16.1046 15 15V12C15 11.4477 14.5523 11 14 11C13.4477 11 13 11.4477 13 12V15H5V7L8 7C8.55228 7 9 6.55228 9 6C9 5.44772 8.55228 5 8 5H5Z" fill="black"/>
                                </svg>
                            </a>
                        </div>

                        <a href={item.link} target="_blank" className="proj__item__img-link">
                            <div className="proj__item__img-wrapper" style={{border: item.border}}>
                                <img src={loader_route} alt="" className="loader" style={{display: loading ? "block" : "none"}}/>
                                <img src={item.img} style={{opacity: loading ? 0 : 1}} onLoad={handleOnLoad} alt="project image" className="proj__item__img"/>
                            </div>
                        </a>
                    </div>

                </div>

        </div>
        )
    }

    let ProjGrid = (props) => {
        return (
            <div className="proj-grid">
                {props.items.map((item, index)=>{
                    return (
                        <ProjItem item={item} key={"ProjItem"+index} index={index}/>
                    )
                })}
            </div>
        )
    }

    return (
        <div className="proj appear">
            <div className="container">
                <div className="proj-inner card card-hover">
                    <ProjGrid items={data}/>
                </div>
            </div>
        </div>
    );
}

export default Proj;